.popup {  
  position: fixed;  
  width: 100%;  
  height: 100%;  
  top: 0;  
  left: 0;  
  right: 0;  
  bottom: 0;  
  margin: auto;  
  background-color: rgba(0,0,0, 0.5);  
}  
.inner {  
  position: absolute;
  left: 20%;
  right: 20%;
  top: 35%;
  bottom: 37%;
  margin: auto;
  border-radius: 20px;
  background: white;
}

.btn-Popup{
  background-color: #3ea396;
  padding: 1px;
  color: #fff;
  margin-top: 45px;
  width: 32%;
  cursor: pointer;
  margin-left: 39px;
  margin-right: 39px;
  font-family: 'Amatic SC';
  font-size: 33px;
}
.btn-Popup:hover{ /* IE 9 */ /* Safari 3-8 */
  transform: scale(1.1); 
}
